html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-barlow);
  line-height: 1.625rem;
  background: var(--background);
  color: var(--light-blue);
  min-height: 100vh;
}

h1,
h2,
h3 {
  margin-top: 0;
  color: var(--dark-blue);
}

a,
a:visited,
a:active {
  text-decoration: none;
}

h1 {
  font-size: 3.5rem;
  font-weight: 900;
  line-height: 3.5rem;
  text-transform: uppercase;
}

h2 {
  font-size: 2rem;
  font-weight: 900;
  line-height: 2.25rem;
  text-transform: uppercase;
}

h3 {
  font-size: 1.5rem;
  font-weight: 900;
  line-height: 1.75rem;
  text-transform: uppercase;
}

:root {
  --dark-orange: hsl(22, 89%, 52%);
  --light-orange: hsl(22, 100%, 69%);
  --dark-blue: hsl(220, 50%, 17%);
  --light-blue: hsl(221, 23%, 58%);
  --grey: hsl(198, 28%, 93%);
  --white: hsl(0, 0%, 100%);
}

:root {
  --font-barlow: "Barlow", sans-serif;
}

.container {
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 3.4375rem;
  margin-bottom: 5.1875rem;
  max-height: 3.625rem;
}

.button {
  padding: 1rem 1.6875rem;
  border-radius: 0.5rem;
}
@media (max-width: 39.9375em) {
  .button {
    padding: 0.6875rem 0.875rem;
  }
}
.button__text {
  font-size: 1rem;
  font-weight: 700;
  line-height: 26px;
  text-transform: uppercase;
  color: var(--white);
}
.button__header {
  padding: 1rem 1.6875rem;
  border-radius: 0.5rem;
  background: var(--grey);
  color: var(--dark-blue);
}
.button__header:hover {
  background: var(--dark-blue);
  color: var(--white);
}
.button__top {
  padding: 1rem 1.6875rem;
  border-radius: 0.5rem;
  background: var(--dark-orange);
}
.button__top:hover {
  background: var(--light-orange);
}

.top {
  overflow-x: hidden;
  min-height: 30rem;
  display: flex;
  align-items: center;
  margin-bottom: 1.875rem;
}
@media (max-width: 39.9375em) {
  .top {
    flex-direction: column;
  }
}
.top__left {
  height: 19.5rem;
  width: 27.8125rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (max-width: 39.9375em) {
  .top__left {
    height: 17.875rem;
    width: 20.4375rem;
  }
}
.top__left--title {
  margin-bottom: 0rem;
}
@media (max-width: 39.9375em) {
  .top__left--title {
    font-size: 3rem;
    line-height: 3rem;
  }
}
.top__left--description {
  padding-right: 1.25rem;
  height: 4.875rem;
}
.top__left--wrapper {
  display: flex;
  align-items: center;
  height: 3.625rem;
}
.top__left--wrapper .top__left--release {
  font-weight: 700;
  text-transform: uppercase;
  margin-left: 2.5rem;
}
@media (max-width: 39.9375em) {
  .top__left--wrapper .top__left--release {
    margin-left: 1.25rem;
  }
}
.top__right {
  min-height: 30rem;
  min-width: 33.75rem;
  margin-left: 7.8125rem;
  background-image: url("../../../assets/desktop/image-keyboard.jpg");
  background-size: cover;
  border-radius: 1.25rem;
}
@media (max-width: 71.8125em) {
  .top__right {
    min-height: 26.5625rem;
    min-width: 29.875rem;
    margin-left: 4.3125rem;
  }
}
@media (max-width: 39.9375em) {
  .top__right {
    margin-top: 4rem;
    min-height: 20.6875rem;
    min-width: 23.25rem;
  }
}

.middle {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10.5rem;
}
@media (max-width: 71.8125em) {
  .middle {
    flex-direction: column;
    align-items: flex-start;
  }
}
@media (max-width: 39.9375em) {
  .middle {
    margin-bottom: 6.9375rem;
  }
}
.middle__photos {
  display: flex;
}
@media (max-width: 39.9375em) {
  .middle__photos {
    transform: translateX(-3.125rem);
  }
}
.middle__photos--phone-keyboard {
  background-image: url("../../../assets/desktop/image-phone-and-keyboard.jpg");
  background-size: cover;
  height: 30rem;
  width: 15.9375rem;
  border-radius: 1.25rem;
  margin-right: 1.5rem;
  background-color: #f16718;
  background-blend-mode: multiply;
}
@media (max-width: 71.8125em) {
  .middle__photos--phone-keyboard {
    height: 20rem;
  }
}
@media (max-width: 39.9375em) {
  .middle__photos--phone-keyboard {
    height: 12.0625rem;
    width: 8.0625rem;
  }
}
.middle__photos--mouse-keyboard {
  background-image: url("../../../assets/desktop/image-glass-and-keyboard.jpg");
  background-size: cover;
  height: 30rem;
  width: 27.8125rem;
  border-radius: 1.25rem;
}
@media (max-width: 71.8125em) {
  .middle__photos--mouse-keyboard {
    height: 20rem;
  }
}
@media (max-width: 39.9375em) {
  .middle__photos--mouse-keyboard {
    height: 12.0625rem;
    width: 13.75rem;
  }
}
.middle__info {
  display: flex;
  flex-direction: column;
  height: 16.5625rem;
  width: 15.9375rem;
}
@media (max-width: 71.8125em) {
  .middle__info {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    max-height: 6.75rem;
    width: 100%;
    margin-top: 4.5rem;
  }
}
@media (max-width: 39.9375em) {
  .middle__info {
    flex-direction: column;
    max-height: none;
    justify-content: flex-start;
  }
}
.middle__info--title {
  margin-bottom: 0;
}
@media (max-width: 39.9375em) {
  .middle__info--title {
    min-width: 15.9375rem;
    min-height: 6.75rem;
    text-align: center;
    margin-bottom: 1.5rem;
  }
}
.middle__info--desc {
  max-height: 4.875rem;
  max-width: 24.875rem;
}
@media (max-width: 39.9375em) {
  .middle__info--desc {
    min-width: 20.4375rem;
    min-height: 6.5rem;
    text-align: center;
  }
}

.bottom {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
}
@media (max-width: 71.8125em) {
  .bottom {
    justify-content: start;
  }
}
@media (max-width: 39.9375em) {
  .bottom {
    flex-direction: column;
    align-items: center;
  }
}
.bottom__card {
  height: 20.1875rem;
  width: 15.9375rem;
  display: flex;
  flex-direction: column;
}
@media (max-width: 71.8125em) {
  .bottom__card {
    width: 21.1875rem;
    margin-right: 0.6875rem;
    margin-bottom: 4.5rem;
  }
}
@media (max-width: 39.9375em) {
  .bottom__card {
    align-items: center;
    text-align: center;
    width: 100%;
    margin-right: 0;
    margin-bottom: 4rem;
  }
}
.bottom__card--compatible {
  display: flex;
  flex-direction: column;
}
@media (max-width: 39.9375em) {
  .bottom__card--compatible {
    align-items: center;
    text-align: center;
  }
}
.bottom__card--compatible .logo {
  width: 4.0625rem;
  height: 4.0625rem;
  border-radius: 1rem;
  background-color: var(--dark-orange);
  background-image: url("../../../assets/shared/icon-compatible.svg");
  background-repeat: no-repeat;
  background-position: center;
  margin-bottom: 3rem;
}
.bottom__card--bluetooth {
  display: flex;
  flex-direction: column;
}
@media (max-width: 39.9375em) {
  .bottom__card--bluetooth {
    align-items: center;
    text-align: center;
  }
}
.bottom__card--bluetooth .logo {
  width: 4.0625rem;
  height: 4.0625rem;
  border-radius: 1rem;
  background-color: var(--dark-orange);
  background-image: url("../../../assets/shared/icon-bluetooth.svg");
  background-repeat: no-repeat;
  background-position: center;
  margin-bottom: 3rem;
}
.bottom__card--battery {
  display: flex;
  flex-direction: column;
}
@media (max-width: 39.9375em) {
  .bottom__card--battery {
    align-items: center;
    text-align: center;
  }
}
.bottom__card--battery .logo {
  width: 4.0625rem;
  height: 4.0625rem;
  border-radius: 1rem;
  background-color: var(--dark-orange);
  background-image: url("../../../assets/shared/icon-battery.svg");
  background-repeat: no-repeat;
  background-position: center;
  margin-bottom: 3rem;
}
.bottom__card--light {
  display: flex;
  flex-direction: column;
}
@media (max-width: 39.9375em) {
  .bottom__card--light {
    align-items: center;
    text-align: center;
  }
}
.bottom__card--light .logo {
  width: 4.0625rem;
  height: 4.0625rem;
  border-radius: 1rem;
  background-color: var(--dark-orange);
  background-image: url("../../../assets/shared/icon-light.svg");
  background-repeat: no-repeat;
  background-position: center;
  margin-bottom: 3rem;
}

.footer {
  width: 100%;
  text-align: center;
  margin-top: 6.125rem;
}
.footer div {
  font-size: 16px;
  line-height: 26px;
  font-weight: 500;
  color: var(--light-blue);
}
.footer div span {
  font-weight: 700;
}